:host {
  box-sizing: border-box;
  display: flex;
  justify-content: end;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  align-items: end;
  gap: var(--mdx-comp-button-group-horizontal-margin-gap-between-default);

  ::slotted(mdx-button) {
    @media screen and (max-width: 768px) {
      width: 51%;
      flex-grow: 1;
    }
  }

  ::slotted(mdx-icon-button) {
    @media screen and (max-width: 768px) {
      flex-shrink: 0;
    }
  }
}
